@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.site-header {
  height: 100px;
  border-bottom: 8px solid #364e72;
  right: 0;
  width: 100%;
  background-color: linear-gradient(132deg, #628bbf, #364e72);
  position: relative;
  /* Safari 4.0 - 8.0 */
  -webkit-animation-name: example;
  -webkit-animation-duration: 2s;
  -webkit-animation-timing-function: linear;
  -webkit-animation-delay: 1s;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
  /* Standard syntax */
  animation-name: example;
  animation-duration: 2s;
  animation-timing-function: linear;
  animation-delay: 1s;
  animation-iteration-count: infinite;
  animation-direction: alternate; }

/* Safari 4.0 - 8.0 */
@-webkit-keyframes example {
  0% {
    background-image: -webkit-linear-gradient(318deg, #c1dbfc, #364e72);
    background-image: linear-gradient(132deg, #c1dbfc, #364e72);
    background-position: 0% 50%; }
  25% {
    background-image: -webkit-linear-gradient(318deg, #7691e4, #364e72);
    background-image: linear-gradient(132deg, #7691e4, #364e72);
    background-position: 100% 50%; }
  50% {
    background-image: -webkit-linear-gradient(318deg, #1b2644, #364e72);
    background-image: linear-gradient(132deg, #1b2644, #364e72);
    background-position: 0% 50%; }
  75% {
    background-image: -webkit-linear-gradient(318deg, #7e7e7e, #364e72);
    background-image: linear-gradient(132deg, #7e7e7e, #364e72);
    background-position: 100% 50%; }
  100% {
    background-image: -webkit-linear-gradient(318deg, #546190, #364e72);
    background-image: linear-gradient(132deg, #546190, #364e72);
    background-position: 0% 50%; } }
@keyframes example {
  0% {
    background-image: -webkit-linear-gradient(318deg, #c1dbfc, #364e72);
    background-image: linear-gradient(132deg, #c1dbfc, #364e72);
    background-position: 0% 50%; }
  25% {
    background-image: -webkit-linear-gradient(318deg, #7691e4, #364e72);
    background-image: linear-gradient(132deg, #7691e4, #364e72);
    background-position: 100% 50%; }
  50% {
    background-image: -webkit-linear-gradient(318deg, #1b2644, #364e72);
    background-image: linear-gradient(132deg, #1b2644, #364e72);
    background-position: 0% 50%; }
  75% {
    background-image: -webkit-linear-gradient(318deg, #7e7e7e, #364e72);
    background-image: linear-gradient(132deg, #7e7e7e, #364e72);
    background-position: 100% 50%; }
  100% {
    background-image: -webkit-linear-gradient(318deg, #546190, #364e72);
    background-image: linear-gradient(132deg, #546190, #364e72);
    background-position: 0% 50%; } }

/* Standard syntax */
@keyframes example {
  0% {
    background-image: -webkit-linear-gradient(318deg, #c1dbfc, #364e72);
    background-image: linear-gradient(132deg, #c1dbfc, #364e72);
    left: 0px;
    top: 0px; }
  25% {
    background-image: -webkit-linear-gradient(318deg, #7691e4, #364e72);
    background-image: linear-gradient(132deg, #7691e4, #364e72);
    left: 0px;
    top: 0px; }
  50% {
    background-image: -webkit-linear-gradient(318deg, #1b2644, #364e72);
    background-image: linear-gradient(132deg, #1b2644, #364e72);
    left: 0px;
    top: 0px; }
  75% {
    background-image: -webkit-linear-gradient(318deg, #7e7e7e, #364e72);
    background-image: linear-gradient(132deg, #7e7e7e, #364e72);
    left: 0px;
    top: 0px; }
  100% {
    background-image: -webkit-linear-gradient(318deg, #546190, #364e72);
    background-image: linear-gradient(132deg, #546190, #364e72);
    left: 0px;
    top: 0px; } }

.logo {
  padding-left: 20px;
  padding-top: 10px;
  width: 90px; }

.cities {
  padding: 10px; }

.text-center {
  text-align: center !important;
  top: 30%;
  left: 30%; }

.cities h1 {
