  background-repeat: no-repeat;
  background-size: cover;
  background-position: center; }

.content {
  font-size: 30px;
  border-radius: 4px;
  height: 100%;
  padding: 3rem;
  text-align: center;
  padding: 2rem;
  background: #ffffff;
  box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.025);
  display: -moz-flex;
  display: -ms-flex;
  -moz-align-items: center;
  -ms-align-items: center;
  align-items: center;
